.responsive__header {
  padding: 0.75rem 1rem;
  background-color: #6b352d; }

.navMenu--horz > * + * {
  border-left: 0;
  padding-left: 2rem;
  margin-left: .4rem; }

.card--framed {
  border: 8px solid #f0e7d6; }

.card--equal {
  min-height: 550px; }

.grow {
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  cursor: pointer; }

.grow:hover {
  -webkit-transform: scale(1.05);
  -ms-transform: scale(1.05);
  transform: scale(1.05); }

.products {
  position: fixed;
  left: 0px;
  top: -100px;
  overflow: hidden;
  z-index: -999;
  width: 100%;
  height: 650px;
  background-size: cover;
  background-position: 50% 50%;
  background-color: #262626;
  background-image: url("../../assets/images/products_bkg.jpg");
  background-repeat: no-repeat; }

.hero__products--content {
  padding: 15% 0; }

.products--wrapper {
  background-color: #f0e7d6; }

.dollarSign {
  font-size: 1rem;
  color: green; }

.price {
  color: green;
  font-size: 2.35rem;
  font-weight: 700;
  position: relative; }

.price:before {
  content: "$";
  font-size: 1.5rem;
  position: absolute;
  top: 5px;
  left: 0;
  margin: -4px; }

.products--grid {
  margin: 0; }

.price--list {
  display: table;
  table-layout: fixed;
  vertical-align: top; }

.price--list > * {
  text-align: left;
  display: table-cell;
  vertical-align: top;
  padding: 0 1rem; }

.price--list--mobile > * {
  text-align: center;
  display: table-cell;
  vertical-align: top;
  margin-left: 0rem;
  padding: 0 1rem; }

.swatch--list > * {
  padding-right: 0.5rem;
  vertical-align: top; }

.mr05 {
  margin-right: 0.5rem; }

.ml05 {
  margin-left: 0.5rem; }

.pr05 {
  padding-right: 0.5rem; }

.pr1 {
  padding-right: 1rem; }

.pl05 {
  padding-left: 0.5rem; }

.aboutUs {
  position: fixed;
  left: 0px;
  top: -100px;
  overflow: hidden;
  z-index: -999;
  width: 100%;
  height: 650px;
  background-size: cover;
  background-position: 50% 50%;
  background-color: #262626;
  background-image: url("../../assets/images/about_bkg.jpg");
  background-repeat: no-repeat; }

.hero__aboutUs--content {
  padding: 15% 0; }

.pl1 {
  padding-left: 1rem; }

.form--wrapper {
  margin: 0 auto;
  width: 75%;
  background-color: #ffffff; }

.form--content {
  text-align: center;
  background-color: #ffffff; }

.label--left {
  text-align: left !important; }

.input--grow {
  width: 60% !important; }

@media screen and (max-width: 48em) {
  .form--wrapper {
    margin: 0 auto;
    width: 100%; }
  .input--grow {
    width: 90% !important; } }

.intro--box {
  padding: 1rem; }

.intro--wrapper {
  background-color: #f0e7d6;
  padding: 1rem; }

.intro--container {
  max-width: 64rem;
  margin: 0 auto;
  padding: 0;
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box; }

@media (min-width: 64rem) {
  .intro--wrapper {
    padding: 1rem; } }

footer {
  background-color: #f7941d;
  padding: 1rem; }



